.container {
  display: grid;

  grid-template-areas:
    "header header header"
    "sidebar main main";
  grid-template-rows: 75px 5fr;
  grid-template-columns: 2fr 10fr;

  max-width: 1260px;
  height: 100vh;
  margin: 0 auto;
}

.conatiner-content {
  grid-area: main;
}

.counter-container {
  display: flex;
  margin: 20px 0px 0px 20px;
}
